import React, { useState, useRef, useEffect } from 'react';
import { Button, Row, Col, Space, Tooltip, message } from 'antd';
import { ReloadOutlined, LeftOutlined } from '@ant-design/icons';
import { Link } from 'gatsby';

import CreateColorPrice from './create_color_price';
import ColorPriceList from './color_price_list';
// ColorPrice 入口组件

const ColorPriceComponent = ({ location }) => {
  // props.location接收传递的参数
  const [goodsId, setGoodsId] = useState(0);
  const reqParams = useRef({ page: 0 });
  const [goodsTitle, setGoodsTitle] = useState('');

  const [reloadListCount, setReloadListCount] = useState(0);

  const [createColorPriceVisible, setCreateColorPriceVisible] = useState(false);

  const [selectColorHandle, setSelectColorHandle] = useState(null);
  const [setVisible, setSetVisible] = useState(null);

  const initReloadList = () => {
    // 刷新列表, 删除所有查询条件
    if (goodsId && goodsId > 0) {
      reqParams.current = { page: 0, goods: goodsId };
      setReloadListCount(reloadListCount + 1);
      console.log('init search 00 ...');
    }else {
      console.log('No goods...');
      // message.error('Error: 请返回, 重新选择产品...');
    }
  }

  useEffect(() => {
    // 接收上一页面传递的参数 商品id 重新查询列表
    // console.log(location);
    try {
      console.log(location.state.goodsobj);
      setGoodsId(location.state.goodsobj.id);
      setGoodsTitle(location.state.goodsobj.name);
      setSelectColorHandle(location.state.selectHandle ? location.state.selectHandle : null);
      console.log(location.state);
    } catch (error) {
      console.log(error);
      message.error('Error: 请重新选择产品...');
    }
    // initReloadList();
  }, [location.state]);

  useEffect(() => {
    initReloadList();
  }, [goodsId]);

  return (
    <div>
      <Row>
        <Col span={14}>
          <div style={{width: '100%', height: 'auto', padding: '0.1rem 0.5rem'}}>
            <Space size="large">
              {setVisible 
                ? null 
                : <Tooltip title="返回"><Link to="/wxgoods"><LeftOutlined /></Link></Tooltip>}
              
              <h4>ColorPrice</h4>
              <Tooltip title="刷新">
                <Button onClick={initReloadList} shape="circle" icon={<ReloadOutlined />}></Button>
              </Tooltip>
            </Space>
          </div>
        </Col>
        <Col span={10}>
          <div style={{ width: '100%', height: 'auto' }}>
            <Button style={{ float: 'right' }} type="link" onClick={() => setCreateColorPriceVisible(true)}>新建</Button>
          </div>
        </Col>
      </Row>
      <Row style={{margin: '1rem 0.2rem'}}>
        <h6>商品标题: {goodsTitle}</h6>
      </Row>

      <ColorPriceList
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        requestParams={reqParams.current}
        selectHandle={selectColorHandle}
        // setVisible={setVisible}
      ></ColorPriceList>

      <CreateColorPrice
        visible={createColorPriceVisible}
        setVisible={setCreateColorPriceVisible}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        goodsId={goodsId}
      ></CreateColorPrice>
    </div>
  )
};

export default ColorPriceComponent;
