const actionButtons = (item, setVisible, setSelectedObj, selectButtonOnClick) => {
  const onClickInfo = () => {
      setSelectedObj(item);
      setVisible(2);
  };
  const onClickSelect = () => {
      // 跳转到 颜色价格页面 传商品id
      // navigate("/wxcolorprice", { state: { goodsobj: item } });
      selectButtonOnClick(item);
      setVisible(0);
  }
  const btnList = [
      <Button onClick={onClickInfo} type="link">详情</Button>,
  ];
  if (selectButtonOnClick) {
      btnList.push(<Button onClick={onClickSelect} type="link">选择</Button>)
  }
  return btnList;
}
// 修改 ~~~~~~~ item keys ~~~~~~;
const renderItem = (item, setSelectedObj, selectButtonOnClick, setVisible, visible) => {
    // 必须 每一行渲染 的组件, 修改 ~~~~~~~ item keys ~~~~~~;
    const textsContent = (
        <div>
            <p>{`库存数量: ${item.stock_num}`}</p>
            <p>{`零售价: ${item.retail_price}`}</p>
            <p>{`团购价: ${item.group_price}`}</p>
            <p>{`代理价: ${item.agent_price}`}</p>
            <p>{`成本: ${item.cost_price}`}</p>
        </div>
    )
    const updateItemObj = {
        id: item.id,
        actions: actionButtons(item, setVisible, setSelectedObj, selectButtonOnClick),
        img: item.property_pic_url,
        avatar: '',
        title: `颜色属性: ${item.color_name}`,
        description: `说明: ${item.explains}`,
        content: textsContent,
    };
    return customRanderItem(updateItemObj, 120);
};

